@font-face {
  font-family: "PTSans";
  font-style: normal;
  font-weight: 400;
  src: local("PTSans"), local("PTSans"), local("PTSans-Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PTSans";
  font-style: normal;
  font-weight: 700;
  src: local("PTSans Bold"), local("PTSans-Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
body {
  width: 100%;
  margin: 0;
}
.page-header {
  display: flex;
  flex-direction: column;
  font-family: "PTSans", "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 40px;
  text-transform: uppercase;
  background-color: #ffffff;
}
.page-header__logo {
  text-align: center;
  line-height: 0;
  margin: 0;
  padding: 0;
  background-color: transparent;
}
@media (min-width: 1200px) {
  .page-header__logo-image {
    width: 185px;
    height: 98px;
  }
}
.main-nav {
  width: 320px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .main-nav {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    width: 1200px;
  }
}
.site-list {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0;
}
.site-list__item {
  text-align: center;
  background-color: #81b3d2;
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding-top: 8px;
  padding-bottom: 7px;
}
.site-list__item--logo {
  margin: 0;
  margin-bottom: -14px;
  padding: 0;
  background-color: transparent;
  border: none;
}
.site-list__item a {
  text-decoration: none;
  color: #ffffff;
}
@media (min-width: 768px) {
  .site-list {
    flex-direction: row;
    width: 100%;
    height: 56px;
    background-color: transparent;
  }
  .site-list__item {
    width: 153px;
    padding-top: 7px;
    background-color: #ffffff;
    border: none;
    order: 0;
  }
  .site-list__item:nth-child(1),
  .site-list__item:nth-child(2) {
    order: 1;
  }
  .site-list__item:nth-child(3),
  .site-list__item:nth-child(4) {
    order: 3;
  }
  .site-list__item--logo {
    width: 156px;
    height: 56px;
    order: 2;
    padding: 0;
  }
  .site-list__item a {
    color: #000000;
    font-size: 14px;
    line-height: 26px;
    background-color: transparent;
  }
}
@media (min-width: 1200px) {
