.advantages {
  width: 320px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  color: #333333;
  font-family: "PTSans", "Helvetica", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
