  padding-top: 21px;
}
.advantages__header {
  padding-left: 32px;
  padding-right: 32px;
  position: relative;
}
.advantages__header::after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 17px 160px 0 160px;
  border-color: #ffffff transparent transparent transparent;
  position: absolute;
  z-index: 100;
  bottom: -45px;
  left: 0;
}
.advantages__slogan {
  font-weight: 700;
  font-size: 18px;
  color: #000000;
}
.advantages__intro {
  padding-top: 9px;
