  margin-bottom: -3px;
}
.advantages__list {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding-left: 0;
  padding-top: 14px;
}
.advantages__title {
  font-size: 18px;
  margin: 0;
  color: #000000;
}
.advantages__number {
  margin-top: 6px;
  margin-bottom: 0;
}
.advantages__description {
  margin-top: 13px;
  line-height: 21px;
  padding-left: 32px;
  padding-right: 32px;
}
.advantages__item {
  padding-bottom: 40px;
}
.advantages__item--city {
  background-color: #81b3d2;
  color: #ffffff;
  padding-bottom: 0;
  padding-top: 57px;
}
.advantages__item--bridge {
  background-color: #81b3d2;
  color: #ffffff;
  padding-bottom: 0;
  padding-top: 40px;
}
.advantages__item--square {
  padding-top: 27px;
  background-color: #efefef;
}
.advantages__item--square::after,
.advantages__item--road::after {
  content: "";
  display: block;
  position: relative;
  top: 13px;
  width: 280px;
  height: 1px;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.1);
}
.advantages__item--facilities {
  padding-top: 0;
  padding-bottom: 0;
}
.advantages__item--tourists {
  padding-bottom: 13px;
}
.advantages__item--road,
.advantages__item--tourists {
  background-color: #efefef;
}
.advantages__item--city .advantages__description {
  padding-left: 20px;
  padding-right: 20px;
}
.advantages__item--city::after {
  content: "";
  display: block;
  width: 100%;
  height: 260px;
  margin-top: 39px;
  background-image: url("../img/main-city-mobile@1x.jpg");
}
.advantages__item--bridge .advantages__description {
  padding-left: 15px;
  padding-right: 15px;
}
.advantages__item--bridge::after {
  content: "";
  display: block;
  width: 100%;
  height: 260px;
  margin-top: 39px;
  background-image: url("../img/main-bridge-mobile@1x.jpg");
}
.advantages__item--city .advantages__title,
.advantages__item--bridge .advantages__title {
  color: #ffffff;
}
.advantages__secondary-list {
  list-style: none;
  padding: 0;
}
@media (min-width: 768px) {
  .advantages {
    padding-top: 29px;
    width: 768px;
  }
  .advantages__header {
    padding-left: 139px;
    padding-right: 139px;
  }
  .advantages__header::after {
    display: none;
  }
  .advantages__slogan {
    font-size: 21px;
  }
  .advantages__list {
    padding-top: 39px;
    display: block;
  }
  .advantages__title {
    font-size: 21px;
    padding-top: 45px;
  }
  .advantages__intro {
    padding-top: 7px;
  }
  .advantages__item {
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;
    min-height: 256px;
    padding-top: 0;
  }
  .advantages__item--square::after,
  .advantages__item--road::after {
    display: none;
  }
  .advantages__item--city,
  .advantages__item--bridge {
    height: 256px;
  }
  .advantages__item--city *,
  .advantages__item--bridge * {
    width: 256px;
    padding-left: 52px;
    padding-right: 52px;
  }
  .advantages__item--city .advantages__description,
  .advantages__item--bridge .advantages__description {
    padding-left: 47px;
    padding-right: 47px;
    padding-top: 7px;
  }
  .advantages__item--city::after,
  .advantages__item--bridge::after {
    display: block;
    width: 512px;
    height: 256px;
    margin-top: 0;
  }
  .advantages__item--city::after {
    background-image: url("../img/main-city-tablet@1x.jpg");
  }
  .advantages__item--bridge::after {
    order: -1;
    background-image: url("../img/main-bridge-tablet@1x.jpg");
  }
  .advantages__secondary-list {
    display: flex;
  }
  .advantages__secondary-list .advantages__item {
    width: 256px;
    padding: 0;
    flex-grow: 1;
  }
  .advantages__secondary-list .advantages__title {
    padding-left: 55px;
    padding-right: 55px;
  }
  .advantages__secondary-list .advantages__description {
    padding-left: 45px;
    padding-right: 45px;
    margin-top: 20px;
  }
  .advantages__item--road .advantages__description {
    padding-left: 55px;
    padding-right: 55px;
  }
  .advantages__br {
    padding-left: 5px;
    padding-right: 5px;
  }
}
@media (min-width: 1200px) {
